@import '~utils/styles/variables.css';

.layout {
    height: 100%;
    width: 100%;
    padding: calc(var(--padding) * 2);
    display: flex;
    flex-direction: column;
}

.layout__title {
    width: 100%;
    padding-bottom: calc(var(--padding) * 1.5);
}

.layout__content {
    width: 100%;
    flex: 1;
}
